import React from "react";
import ReactDOM from "react-dom";
import { createBrowserHistory } from "history";
import {
  Route,
  Switch,
  Redirect,
  HashRouter,
  BrowserRouter,
} from "react-router-dom";
import { Provider } from "react-redux";
import { PersistGate } from "redux-persist/lib/integration/react";

import "assets/scss/material-kit-react.scss?v=1.9.0";

// pages for this product
import DataView from "views/ViewDataPage";
import LandinpPage from "./views/LandingPage/LandingPage";
import Login from "./views/LoginPage/LoginPage";
import UploadCSV from "./views/UploadCSV";
import configureStore from "./store/index";

const { store, persistor } = configureStore();

var hist = createBrowserHistory();

ReactDOM.render(
  <Provider store={store}>
    <PersistGate persistor={persistor}>
      <HashRouter history={hist}>
        <Switch>
          <Route exact path="/" render={() => <Redirect to="/adminLogin" />} />
          <Route path="/DataView/:id" component={DataView} />
          <Route path="/adminLogin" component={Login} />
          <PrivateRoute path="/adminUploadCSV" component={UploadCSV} />
        </Switch>
      </HashRouter>
    </PersistGate>
  </Provider>,
  document.getElementById("root")
);

function PrivateRoute({ component, ...rest }) {
  if (1 === 0) {
    return (
      <Redirect
        to={{
          pathname: "/adminLogin",
          // state: {
          //   from: props.location,
          // },
        }}
      />
    );
  } else
    return (
      <Route
        {...rest}
        render={(props) => React.createElement(component, props)}
      />
    );
}
